import React from 'react'
import AniLink from 'gatsby-plugin-transition-link/AniLink';
import { StaticImage } from 'gatsby-plugin-image';

import Box from 'components/Box';
import Flex from 'components/Flex';
import { responsive } from '../components/ThemeProvider/theme';
import { useToken } from '@chakra-ui/react';

const Header = ({ onOpen, headerColor, logoWidth, ...props }) => {
  // console.log(headerColor)
  const [teal] = useToken('colors', ['custom.teal'])
  return (
    <Flex
      as="header"
      position="absolute"
      bg="transparent"
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent={responsive('center', 'flex-start')}
      zIndex="docked"
      overflow="hidden"
      mx={responsive('0.5em', '2.5em')}
      mt={responsive('2.25em', '2.25em')}
      {...props}
    >
      <Box.Relative width={logoWidth || responsive('5.625em', '6.25em')}>
        <AniLink to="/" paintDrip hex={teal}>
          {headerColor === 'white' ? (
            <StaticImage alt="GoShare Logo" placeholder="tracedSVG" layout="fullWidth" src="white_logo.png" tracedSVGOptions={{ color: 'white' }} />
          ) : (
            <StaticImage alt="GoShare Logo" placeholder="tracedSVG" layout="fullWidth" src="teal_logo.png" tracedSVGOptions={{ color: '#06F3F9' }} />
          )}
        </AniLink>
      </Box.Relative>
    </Flex>
  )
}

export default Header
