import React from 'react'
import { StaticImage } from 'gatsby-plugin-image'
import Box from '../components/Box'
import { responsive } from '../components/ThemeProvider/theme'

const Footer = ({ logoWidth, ...props }) => {
  return (
    <Box
      textAlign="center"
      // pt={responsive('1.625rem', '2.375em')}
      // pb={responsive('3.75rem', '3.625em')}
      {...props}
    >
      <Box.Inline width={logoWidth || responsive('5.25em', '6em')}>
        {props.color === 'white' ? (
          <StaticImage placeholder="tracedSVG" alt="Gogoro Logo" src="gogoro_white.png" tracedSVGOptions={{ color: 'white' }} />
        ) : (
          <StaticImage placeholder="tracedSVG" alt="Gogoro Logo" src="gogoro.png" tracedSVGOptions={{ color: 'black' }} />
        )}
      </Box.Inline>
    </Box>
  )
}

export default Footer
