import React, { forwardRef } from 'react';
import { Button } from "@chakra-ui/react";

import Link from './Link'
import { responsive } from './ThemeProvider/theme';

const ReButton = forwardRef(({ href, ...props }, ref) => {
  if (href) return <Button as={p => <Link {...p} href={href} />} href={href} ref={ref} {...props} />
  return <Button ref={ref} {...props} />
})

ReButton.defaultProps = {
  // colorScheme: theme.colors.custom.blue,
  color: 'custom.darkBlue',
  py: '1rem',
  bg: 'custom.gray',
  borderRadius: 'full',
  height: 'auto',
  _hover: {
    bg: 'red.400',
  },
  _focus: {
    bg: 'green.400',
  },
  _active: {
    bg: 'custom.teal',
  },
  fontSize: responsive('0.875em', '1.375em')
}

ReButton.Secondary = props => (
  <ReButton colorScheme="green" {...props} />
)

ReButton.Danger = props => (
  <ReButton colorScheme="red" {...props} />
)

ReButton.Pink = props => (
  <ReButton
    py="0.75rem"
    color="white"
    bg="custom.pink"
    _hover={{ bg: 'custom.pink' }}
    {...props}
  />
)

ReButton.Yellow = props => (
  <ReButton
    py="0.875rem"
    px="4rem"
    fontSize={responsive('1.25rem', '1.625rem')}
    lineHeight="1"
    color="custom.darkBlue"
    bg="custom.lightYellow"
    _hover={{ bg: 'custom.lightYellow' }}
    {...props}
  />
)

ReButton.Orange = props => (
  <ReButton
    color="white"
    bg="custom.orange"
    border="1px solid"
    borderColor="custom.orange"
    _hover={{ bg: 'custom.orange' }}
    {...props}
  />
)

ReButton.Transparent = props => (
  <ReButton
    bg="transparent"
    _focus={{ bg: "transparent" }}
    _hover={{ bg: "transparent" }}
    _active={{
      bg: "transparent",
    }}
    {...props}
  />
)

export default ReButton
